* {
  margin: 0;
  padding: 0;
}
*::after {
  margin: 0;
}
*::before {
  margin: 0;
}
body {
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  min-height: 100vh;
  box-sizing: border-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  font-family: "CleanvertisingLight", sans-serif;
  color: #000;
  padding: 1px;
}
@media screen and (min-width: 481px) {
  body {
    padding: 2px;
  }
}
body .header {
  box-sizing: border-box;
  background: linear-gradient(to bottom, #b4d3d6, #94abad);
  padding-bottom: 5px;
}
@media screen and (min-width: 481px) {
  body .header {
    padding-bottom: 8px;
  }
}
body .main-content {
  height: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
  box-sizing: border-box;
  display: -webkit-inline-flex;
  display: -moz-inline-flex;
  display: -ms-inline-flex;
  display: -o-inline-flex;
  display: inline-flex;
  flex-direction: row;
  justify-content: space-around;
  flex: 1;
}
